body.no-scroll {
  overflow: hidden;
}


.background-video {
  position: fixed;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -1;
  opacity: 0.8;
  object-fit: cover;
}

.homecontent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  height: 100vh;
  padding-left: 40px;
  padding-bottom: 200px;
  overflow: hidden;
}

.title {
  font-family: "Arial", sans-serif;
  font-size: 4rem;
  text-align: left;
  margin-bottom: 50px;
  color: #ff6600;
  text-shadow: 2px 2px 4px #000000;
}

.description {
  background-color: rgba(211, 211, 211, 0.7);
  border-radius: 15px;
  padding: 20px;
  font-size: 1rem;
  text-align: left;
  line-height: 1.5;
  max-width: 700px;
  margin-bottom: 10px;
}

.map-container {
  position: fixed;
  top: 10%;
  right: -2%;
  height: 85%;
  width: 55%;
  overflow: hidden;
}

.map {
  width: 90%;
  height: 100%;
  border: none;
}